
.App {
  text-align: center;
  width: 100%;
}

.header {
  position: fixed;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  width: 100%;
  z-index: 1;
}

.main {
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.logo {
  height: 20vmin;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  margin: 0px;
}

.overlay {
  position:absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
}

